.research-page {
    // padding-top: 10%;
    // margin-bottom: 10%;
  max-width: 80%;
  // margin: clamp(2rem, 10vh, 5rem) auto; // Responsive top-bottom spacing
//   padding: 0 clamp(1rem, 5vw, 2rem); // Responsive left-right spacing
  // height: 80vh;
  overflow-y: auto;
  scroll-behavior: smooth;
  .page-title {
    margin-bottom: clamp(1rem, 5vh, 2.5rem);
    font-size: clamp(2rem, 5vw, 3rem); // Responsive font size
    font-weight: bold;
    text-align: center;
  }

  .research-list {
    display: flex;
    flex-direction: column;
    gap: clamp(1rem, 2vw, 1.8rem); // Responsive gap between boxes
    // margin-bottom: 10%;
    .citation-box {
      border: 2px solid #000000;
      border-radius: 8px;
      padding: clamp(1rem, 2vw, 1.5rem);
      background:#bbd1c5;
      transition: border-color 0.3s ease;

      &:hover {
        border-color: #ffd700;
      }

      .citation-header {
        display: flex;
        align-items: center;
        gap: clamp(0.5rem, 1vw, 1rem);
        margin-bottom: clamp(0.5rem, 1vh, 2.5rem);

        .id-number {
          font-size: clamp(1.2rem, 2.2vw, 2rem);
          font-weight: bold;
          color: #444;
        }

        .paper-title {
          font-size: clamp(1.2rem, 2.2vw, 2rem);
          font-weight: bold;
          color: #000000;
        }
      }

      .citation-text {
        font-size: clamp(1.5rem, 2vw, 2rem);
        color: #000000;
        line-height: 1.6;
      }

      .paper-link {
        display: inline-block;
        font-size: clamp(1rem, 1.8vw, 2rem);
        color: #0056b3;
        text-decoration: underline;
        margin-left: clamp(2rem, 2vw, 2rem);

        &:hover {
          color: #003580;
        }
      }
    }
  }
}


/* Mobile improvements: natural flow (no inner scroll), responsive boxes and stacked headers */
@media only screen and (max-width: 600px) {
  .research-page {
    max-width: 95%;
    margin: 0 auto;
    margin-top: 15%;
    /* Ensure the page can scroll when content overflows */
    min-height: 80vh;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    padding: 2rem 1rem 2rem; /* top padding so title sits a bit below */
  }

  .page-title {
    font-size: clamp(1.6rem, 5vw, 2.4rem);
    margin-top: 0.6rem; /* push the title a bit lower */
    margin-bottom: 1rem;
    text-align: center;
  }

  .research-list {
    gap: clamp(0.5rem, 2.5vw, 1.25rem);

    .citation-box {
      padding: clamp(0.75rem, 2.2vw, 1rem);
    
      .citation-header {
        /* Stack id and title on mobile to avoid cramped horizontal layout */
        // flex-direction: column;
        align-items: flex-start;
        gap: clamp(0.25rem, 1.2vw, 0.75rem);
        margin-bottom: clamp(0.5rem, 1vh, 1rem);

        .id-number {
          font-size: clamp(1rem, 3.2vw, 1.4rem);
        }

        .paper-title {
          font-size: clamp(1.05rem, 3.6vw, 1.6rem);
        }
      }

      .citation-text {
        font-size: clamp(1rem, 3.5vw, 1.15rem);
        line-height: 1.5;
      }

      .paper-link {
        display: block; /* place link on its own line */
        margin-left: 0;
        margin-top: 0.6rem;
        font-size: clamp(0.95rem, 3.2vw, 1.05rem);
        text-align: left;
      }
    }
  }
}
